@import url('https://fonts.googleapis.com/css2?family=Wallpoet&display=swap');

body{
  font-family:'Wallpoet', sans-serif ;
  color: #FBCD08;
  background-color: #372E18;
}

.app-container{
  display: flex;
  justify-content: center;
  align-items: top;
  height: 100vh;
  border: #FBCD08;
  border-radius: 10px;
  border-style: dashed;
}

.content{
  margin-left: 250px;
  padding: 20px;
  width: 100%;
  transition: margin-left 0.3s;
  
}

.logo-container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

.vertical-bar-collapsed + .content{
  margin-left: 50px;
}

.divider{
  border: solid;
  border-color: aliceblue;
  border-radius: 1px;
  border-width: 1px;
  background-color: #FBCD08;
  height: 5px;
  justify-content: center;
  width: 950px;
  margin-left: 0px;
}

.footer{
  border: solid;
  border-color: aliceblue;
  background-color: antiquewhite;
  border-radius: 1px;
  border-width: 1px;
  justify-content: center;
  height: 10px;
  width: 1200px;
  
}

.infoContainer{
  border: 2px solid #FBCD08;
  padding: 20px;
  margin: 20px auto;
  max-width: 1000px;
  max-height: 1000px;
  border-radius: 10px;
  background-color: #372E18;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  font-size: large;
}
